//mixins
@mixin text-template($size, $family, $color) {
font-size:$size;
color:$color;
font-family:$family;
} //end text-template


@mixin background($img, $color, $screen, $size ) {
background-image: linear-gradient(to right bottom, rgba($color, $screen), rgba($color, $screen)), url($img);
background-size: $size;
background-repeat:no-repeat;
background-position:center center;
} //end background mixin

@mixin background-double($img1,$img2, $color, $screen, $size1, $size2) {
background-image: linear-gradient(to right bottom, rgba($color, $screen), rgba($color, $screen)), url($img1),url($img2);
background-size: $size1, $size2;
background-repeat:no-repeat, no-repeat;
background-position:center center, center center;
}


@mixin transition {
transition: all .2s ease-in-out;
}

@mixin darken($baseColor,$per){
background-color: darken($baseColor, $per)}

//darken dispose
@mixin darken($baseColor,$per){
background-color: darken($baseColor, $per)}

@mixin icons{
margin-right:20px ;
font-size: 15px;
color:$bh-green-3;
speak: none;
font-style: normal;
font-weight: normal;
font-variant: normal;
text-transform: none;
line-height: 1;
}
// icons responsive sizes
@mixin icons-r($size,$color){
margin-right:20px ;
font-size: $size * 0.8;
color:$color;
speak: none;
font-style: normal;
font-weight: normal;
font-variant: normal;
text-transform: none;
line-height: 1;
@include responsive(phone){
font-size:$size * 0.85 ;
}
@include responsive(tablet){
font-size:$size * 0.9 ;
}
@include responsive(laptop){
font-size: $size * 0.95;
}
@include responsive(desktop){
font-size:$size;//~21
}
}//end icons-mixin-responsive

