.search-results {
display:flex;
align-items: flex-start;
justify-content: start;
overflow:hidden;
position:relative;
width: 100%;
//@include background(nil,purple,0.04,cover);
height: min-content;
margin-bottom:50px;
flex-wrap:wrap; 

// header media queries
@include responsive(small-phone){
padding: 0px 10px;
}
@include responsive(phone){}
@include responsive(tablet){}
@include responsive(big-tablet){}
@include responsive(desktop){}//end desktop
@include responsive(big-desktop){}
@include responsive(developer){}

& > h1 {
display:none;
width:100%;
font-family: $title-blog;
font-weight:700;
font-size: $supersized;
margin: 0px 0px 0px 0px;
text-align:center;
color:$blog-headlines;

@include responsive(small-phone){
margin: 0rem 0rem 1rem 0rem;
font-size: $gargantuan;
}//575px
@include responsive(phone-500){
