.navigation {
grid-column: 1 / -1;
grid-row: 1 / 3;
align-self:start;
display:grid;
grid-template-columns:1fr; 
grid-template-rows:auto;                
//min-height:7vh;
min-height:4.7025rem;
width:100%;
background:$navigation-background;
align-items:center;
overflow:hidden;

@include responsive(big-tv){
grid-template-columns:1fr minmax(auto, 2500px) 1fr;
}//2500px

& > .navigation_container{
grid-column:1 / -1; 
grid-row:1; 
display:grid;
grid-template-columns:1fr 1fr 8fr;
grid-template-rows:auto;
background:$navigation-background;
height:100%;
align-items: center;

@include responsive(tablet){
grid-row: 1;
}

@include responsive(tablet){
grid-template-columns:1fr 8fr 1.5fr;
}

@include responsive(big-tv){
grid-column: 2;
}//2500px

& > a.brand{
grid-column:1;
grid-row:1;
display:inline-block;
width:80px;
height:40px;
margin: 0px 10px 0px 10px;

@include responsive(phone){
height:40px;
width:80px;
}

& > .footer-logo{
border-radius:$header-b-rad;
height:100%;
width:100%;

& > .img {
width:100%;
height:100%;
object-fit:cover;
object-position:center center;
border-radius:$rad-normal;
display:flex;
transition: all .2s ease-in-out;
align-items:center;
justify-content:center;
}//end image

}//end footer logo picture tag

}//end develop link <a>

& > a.brand:hover{
transform: scale(1.2);
}

& > .hamburger{
grid-column:2;
grid-row:1;
display:block;
border:none;
background:transparent;

& > .fa-bars{
font-size:xx-large;
color:$color-primary;
}

@include responsive(tablet){
display:none;
}
}//end hamburger

& > hamburger:focus, & > button:focus{
outline:none;
}

& > .is-active{
border: 1px solid #fff;
border-radius:$header-b-rad;
}

& > .main-nav{
grid-column:1 / 4;
grid-row:2;
display:none;
justify-self:start;
align-self:center;
width:100%;

@include responsive(tablet){
grid-column:2;
grid-row:1;
justify-self:start;
display:flex;
flex-direction:row;
background-color:transparent;
}

& > ul{
display:flex;
flex-direction:column;
margin-top:15px;

@include responsive(tablet){
flex-direction:row;
margin-top:0px;
align-items: center;
}

//this is .nav-item
& > li{
margin-left:0px;

@include responsive(big-tv){
margin-top:10px;
}//big-tv
}//end li
}//end ul
}//end main nav

& > .drop-down{
display:block;
}

& > .phone{
grid-column:3;
grid-row:1;
text-align:right;
align-self:center;
justify-self:end;
width:95%;
display:flex;
justify-content:flex-end;

& > a{
align-self:center;
}
}//phone

.nav-link {
color:$color-primary;
font-weight:bold;
//font-family:$font-family-ads;
font-family:$font-family-nordique;
padding: .5rem 1rem;

@include responsive(iphone-5){
font-size:$big;
}

@include responsive(galaxy-fold){
font-size:$normal;
}

@include responsive(phone){
font-size:$large;
}

@include responsive(tablet){
font-size:$normal;
padding: .5rem .7rem;
}

@include responsive(big-tablet){
font-size:$big;
padding: .5rem 1rem;
}

@include responsive(desktop){
font-size:$med;
}
@include responsive(big-desktop){
font-size:$large;
}

@include responsive(developer){
font-size:$large;
}

@include responsive(tv){
font-size:$large;
}

@include responsive(big-tv){
font-size:$supersized;
}

}//end nav-link

.navbar-custom {
background-color: rgba(0,0,0, 0.9);
min-height:7vh;
width:100%;
}

// for local hrefs
a.anchor {
display: block;
position: relative;
top: 25px;
visibility: hidden;
}//end anchor
}//end navigation container
}//end navigation
