html {
box-sizing: border-box;
font-size: 16px;
line-height: 1.5;
display:block;

@include responsive(small-phone) {
font-size: .9rem;
}
@include responsive(phone) {
font-size:  .9rem;
}
@include responsive(tablet) {
font-size: 1rem;
}
@include responsive(laptop) {
font-size: 1rem;
}
@include responsive(desktop) {
font-size: 1rem;
}
@include responsive(big-desktop) {
font-size: 1rem;
}

} // end html

body {
display:grid;
grid-template-columns: 1fr;
grid-template-rows:auto auto auto;
font-family: $font-family-reset;
color: $color-text;
font-weight: 400;
line-height: 1.5;
min-height:100vh;
//background-image: linear-gradient(180deg, rgba($mogb, .3), rgba($mogb, .1)), url();
background-color: $color-primary;
$mogb:#4C5D73;
$vligb:#90B0D9 ;
$gwh:#ECEDEC ;
overflow-x:hidden;
box-sizing:border-box;

@include responsive(big-tv){
grid-template-columns: 1fr minmax(auto, 2500px) 1fr;
}

} // end body

.wrapper {
grid-column:1 / -1;
grid-row:1;
display:grid;
width:100%;
grid-template-columns: repeat(12, [col-start] 1fr);
grid-template-rows:auto;

@include responsive(big-tv){
grid-column:2;
}

} // end wrapper

.sticky {
//position: fixed;
position: sticky;
//left: 0;
//right: 0;
top: 0;
z-index: 10;
}//end sticky

#bh-form{

& > .form-group{
margin-bottom: 0px;

& > .bh-btn {
color: #fff;
background-color:$bh-button-color; 
border-color: $bh-button-color;
width: auto;
padding: .5rem .7rem .4rem .7rem;
margin: 1.2rem 0rem 1.2rem 0rem;
border-radius: 4px 4px 4px 4px;
font-size: 0.935rem;
font-family: $bh-button-font;
font-weight: 400;
color: #5a5a5a;

@include responsive(small-phone){margin-top:20px;}//575px
@include responsive(phone-500){}//411px
@include responsive(pixel-2){}//411px
@include responsive(iphone-x){}//376px
@include responsive(galaxy){}//361px
@include responsive(iphone-5){}//321px
@include responsive(galaxy-fold){}//280px
@include responsive(phone){margin-top:20px;}//575px

}//end bh-btn

& > input{
margin-top:0px;
//margin-top:20px;
//font-size: ;
@include responsive(small-phone){}//575px
@include responsive(phone-500){}//411px
@include responsive(pixel-2){}//411px
@include responsive(iphone-x){}//376px
@include responsive(galaxy){}//361px
@include responsive(iphone-5){}//321px
@include responsive(galaxy-fold){}//280px
@include responsive(phone){}//575px

}//end input

}//end .form-group
}//end bh-form

.blog-heading{
padding:0rem;
margin-bottom:.5rem;
width:100%;
text-align:center;

@include text-ph($title-blog, $massive, 600, $blog-headlines);

@include responsive(desktop){
margin-bottom:1rem;
}//end desktop

@include responsive(tv){
margin-bottom:1.3rem;
}//end developer
}//end .blog-heading

.p-news{
width:100%;
height:auto;
@include text-ph($font-family-arimo, $jumbo, 400, $color-p-news);
}//end p-news

#disqus_thread{
width: 100%;
max-width: 800px;
//reverse cascade
@include responsive(small-phone){
margin-top: 2rem;
}//500px x 1.15 = 574px
//forward cascade
@include responsive(tablet){
margin-top: 3rem;
}//650px x 1.18 = 768px
@include responsive(desktop){
margin-top: 4rem;
}//1030px x 1.16 = 1200px
@include responsive(developer){
margin-top: 4.5rem;
}//1400px x 1.142 = 1600px
@include responsive(tv){
margin-top: 5rem;
}//1920px x 1.041 = 2000px
}

.feature-no-image{
display:none;
}
