.main{
width:100%;
height:auto;
min-height:50vh;

@include responsive(small-phone){
padding: 0px 10px;
}//575px
@include responsive(phone){
padding: 0px;
}//280px

& > .banner-title{
display:flex;
align-items: flex-end;
justify-content:center;
width:100%;
height:fit-content;

@include responsive(small-phone){
text-align:center;
text-indent:0%;
}//575px
@include responsive(phone-500){}//411px
@include responsive(pixel-2){}//411px
@include responsive(iphone-x){}//376px
@include responsive(galaxy){}//361px
@include responsive(iphone-5){}//321px
@include responsive(galaxy-fold){}//280px
@include responsive(phone){}//end phone
@include responsive(tablet-min){}//end phone

@include responsive(tablet){
text-align:right;
justify-content:start;
text-indent:40%;
display:none;
}//end tablet

& > h1{
font-family: $title-blog;
font-weight:700;
margin:0rem;
width:100%;
text-align:center;
//background:blue;

@include responsive(small-phone){
margin: 0rem 0rem 1rem 0rem;
font-size: $gargantuan;
}//575px
@include responsive(phone-500){
font-size: $supersized;
}//411px
@include responsive(pixel-2){
margin: 0rem 0rem 0.5rem 0rem;
font-size: $jumbo;
}//411px
@include responsive(iphone-x){}//376px
@include responsive(galaxy){}//361px
@include responsive(iphone-5){
font-size: $larger;
}//321px
@include responsive(galaxy-fold){}//280px

@include responsive(phone){
width:max-content;
margin: 0rem 0rem 1rem 0rem;
}
@include responsive(phone) {
font-size: $gargantuan;
}

@include responsive(tablet-min) {
font-size: $gargantuan;
font-size:$grande;
margin: 0rem 0rem 1rem 0rem;
}

}//end h1
} //end banner-title

//recent posts
& > .posts {
width:100%;
height:auto;
padding:0px 0px 0px 0px;
margin:0px 0px 15px 0px;

// header media queries
@include responsive(small-phone){
margin: 0px 0px 0px 0px
}//end small-phone
@include responsive(desktop){
padding:0px 15px 0px 15px;
}//end desktop
@include responsive(big-desktop){
margin:0px 0px 15px 0px;
}//end big-desktop

&  a.title, &  a.title:hover{
color: #222;
text-decoration: none;
}//title link

& > div{
display:flex;
justify-content:center;
flex-wrap:wrap;
flex-direction: row;

& > ul{
margin-bottom:20px;

& > .page-item.active > .page-link {
z-index: 1;
color: #fff;
color: $color-preview-tags-text;
background-color: $color-preview-tags-background;
border-color: transparent;
font-family:$tags-text;
}

& > .page-item.disabled > .page-link {
color:$color-preview-tags-text;
pointer-events: none;
cursor: auto;
background-color: #fff;
border-color: #dee2e6;
font-family:$tags-text;
}

& > li{
& > .page-link {
color:$color-preview-tags-text;
font-family:$tags-text;
}//page-link

}//end li
}//end ul
}//end div

}//end posts

}//end main
