body {
display:grid;
grid-template-columns: 1fr;
//grid-template-rows: 7vh auto auto;
grid-template-rows: auto auto auto;
background:$color-body; 
//min-height:100vh;
@include responsive(big-tv){
grid-template-columns: 1fr minmax(auto, 2500px) 1fr;
}

& > .navigation {
grid-column: 1 / -1;
@include responsive(phone){
grid-row: 1;
}
@include responsive(tablet){
grid-row: 1;
}

}//end navigation-blog

& > .footer {
grid-column:1 / -1;
grid-row:3;

@include responsive(tablet) {
grid-row:3;
}
}//end footer

}//end body

.wrapper {
grid-column:1 / -1;
grid-row:2;
display:grid;
width:100%;
background:$color-wrapper-background;  

@include responsive(small-phone){
grid-template-columns: 6px repeat(12, 1fr 6px);
}
@include responsive(pixel-2){
}
@include responsive(galaxy){
grid-template-columns: 5px repeat(12, 1fr 5px);
}
@include responsive(iphone-5){
grid-template-columns: 4px repeat(12, 1fr 4px);
}
@include responsive(galaxy-fold){
}
//forward cascade
@include responsive(phone){
grid-template-columns: 8px repeat(12, 1fr 8px);
}
@include responsive(tablet){
grid-template-columns: 16px repeat(12, 1fr 16px);
grid-template-rows: auto auto auto minmax(0px, auto) minmax(auto, 1fr) auto;
}
@include responsive(big-tablet){

}
@include responsive(laptop){

}
@include responsive(desktop){
//grid-template-rows: auto auto auto minmax(0px, auto) minmax(0px, auto) auto;
grid-template-rows: auto auto auto minmax(0px, auto) minmax(auto, 1fr) auto;

}
@include responsive(big-desktop){

}
@include responsive(developer){
}
@include responsive(tv){
}
@include responsive(big-tv){
grid-column:2;
}

